.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.nav-bar {
  display: flex;
  position: fixed;
  top: 10px;
  right: 20px;
  color: white;
  z-index: 1;
  flex-direction: row;
}
.nav-bar a {
  font-size: 18pt;
  margin-right: 10px;
  padding: 0 6px 0 6px;
  font-family: "Raleway-semibold", serif;
  border: 1px solid white;
  border-radius: 4px;

}
.nav-bar a:hover {
  color: #61dafb;
  cursor: pointer;
}
a:checked {
  color: red;
}